<template>
  <div class="flex mt-1">
    <v-checkbox
      v-model="checked"
      color="#00194D"
      hide-details
      :ripple="false"
      :label="labelText"
      :disabled="isCheckboxDisabled"
      @click="changeValue()"
    ></v-checkbox>
  </div>
</template>

<script>
export default {
  name: 'FilterCheckbox',
  props: ['labelText', 'filterName'],
  created() {
    this.$store.commit('setFilterValues', {
      filter: this.filterName,
      val: false
    });
  },
  methods: {
    changeValue() {
      this.$store.commit('setFilterValues', {
        filter: this.filterName,
        val: this.checked
      });
    }
  },
  data() {
    return {
      checked: false
    };
  },
  computed: {
    isCheckboxDisabled() {
      if (this.filterName === 'with_depot') {
        return this.$store.state.filtersValues['with_reg'];
      } else if (this.filterName === 'with_reg') {
        return this.$store.state.filtersValues['with_depot'];
      }
      return false;
    }
  }
};
</script>

<style lang="sass">
.theme--light.v-icon
  color: $color-dark-blue

.v-input
  .v-label
    font-size: 14px
    color: $color-dark-blue
    margin-left: -5px
    margin-right: 5px
    margin-top: 2px
  &--is-disabled
    .v-label
      color: $color-blue-grey
</style>
