<template>
  <div>
    <p class="filter-label">{{ labelText }}</p>
    <v-select
      v-model="value"
      :items="itemsRecount"
      color="#FF7141"
      :item-text="`${keyText}`"
      :item-value="`${keyValue}`"
      :style="{
        width: inputWidth
      }"
      solo
      flat
      hide-details
      :class="{
        'disable-events':
          ($store.state.currentTableType === 'rep_9' &&
            !$store.state.filtersValues['type_rep_9'] &&
            filterName === 'time_rep_9') ||
          ($store.state.currentTableType === 'rep_15' &&
            !$store.state.filtersValues['report-15_v_1-cut'] &&
            filterName === 'report-15_v_1-timefield')
      }"
      @change="changeValue()"
    >
      <template #item="{ item }">
        <span class="select-item-text">{{ item[`${keyText}`] }}</span>
      </template>
    </v-select>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'SelectFilter',
  created() {
    this.setModelValue();
    if (this.isRemote) {
      this.fetchItems();
    } else {
      if (this.filterName === 'time_rep_2') {
        this.items = this.$store.state.timeFilterCurrentValue;
      } else this.items = this.localItems;
    }
    this.$store.commit('setFilterValues', {
      filter: this.filterName,
      val: this.value
    });
  },
  methods: {
    fetchItems() {
      axios.get(this.filterUrl).then((response) => {
        this.items = response.data.data;
      });
    },
    changeValue() {
      this.$store.commit('setFilterValues', {
        filter: this.filterName,
        val: this.value
      });
      if (
        this.filterName.includes('type_rep_') &&
        (this.filterName.substring(9) === '2' ||
          this.filterName.substring(9) === '3')
      ) {
        switch (this.value) {
          case 0: //
            this.$store.commit(
              'setTimeFilterCurrentValue',
              this.$store.state.timeFilterValues[1]
            );
            break;
          case 1: //
            this.$store.commit(
              'setTimeFilterCurrentValue',
              this.$store.state.timeFilterValues[0]
            );
            break;
        }
      }
    },
    setModelValue() {
      this.value = this.defaultValue ? this.defaultValue : this.value;
    }
  },
  props: [
    'labelText',
    'filterName',
    'isRemote',
    'filterUrl',
    'localItems',
    'keyText',
    'keyValue',
    'inputWidth',
    'defaultValue'
  ],
  data() {
    return {
      value: 0,
      items: []
    };
  },
  computed: {
    itemsRecount() {
      let fullTime;
      if (
        this.filterName.includes('time_rep_') &&
        (this.filterName.substring(9) === '2' ||
          this.filterName.substring(9) === '3')
      ) {
        fullTime = this.localItems.map(function (item) {
          return { id: item.id, name: item.name + ':00' };
        });
      }
      return fullTime || this.items;
    }
  }
};
</script>

<style>
.v-input input {
  max-height: 17px;
  color: #00194d !important;
}

.theme--light.v-select .v-select__selections {
  color: #00194d !important;
  vertical-align: middle;
  max-height: 17px;
  padding-top: 2px;
}
</style>

