<template>
  <v-dialog v-model="infoModal" width="420" persistent>
    <v-card>
      <v-toolbar elevation="0" height="170">
        ! <br />
               .
      </v-toolbar>
      <v-card-actions class="justify-center">
        <v-btn
          class="modal-close"
          color="#4D6C99"
          min-height="29px"
          min-width="134px"
          small
          dark
          depressed
          @click="infoModal = false"
          :ripple="false"
        >
          
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'InfoModal',
  data() {
    return {
      infoModal: false
    };
  },
  computed: {
    getRep9Type() {
      return this.$store.state.filtersValues['type_rep_9'];
    }
  },
  watch: {
    getRep9Type(newVal) {
      if (newVal === 1) {
        this.infoModal = true;
      }
    }
  }
};
</script>

<style lang="sass" scoped>
.v-card
  height: 240px

.v-toolbar
  position: relative
  font: 24px/27px $body-font-family
  text-align: center
  color: $color-dark-blue
  display: flex
  justify-content: center

.v-card__actions
  background-color: $color-light-grey-40
  height: 70px
</style>
