<template>
  <div class="mr-5 range-date" v-if="splitted">
    <div class="range-date-half">
      <div>
        <p class="filter-label">{{ labelText }} </p>
        <v-menu
          v-model="fromDateMenu"
          class="mt-2"
          transition="scale-transition"
          offset-y
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              solo
              flat
              hide-details
              readonly
              v-model="dateRangeTextFrom"
              v-on="on"
              @click:append="showCalendar('from')"
            >
              <template v-slot:append>
                <v-icon
                  color="#FF7141"
                  class="cal-icon"
                  @click="showCalendar('from')"
                  >icon-calendar</v-icon
                >
              </template>
            </v-text-field>
          </template>
          <v-date-picker
            v-model="value[0]"
            color="#4D6C99"
            header-color="#4D6C99"
            elevation="3"
            show-adjacent-months
            :weekday-format="getDay"
            @input="[setValueInStore(value)]"
            first-day-of-week="1"
          />
        </v-menu>
      </div>
      <v-select
        v-model="hoursValue[0]"
        :items="fullTimeValues"
        color="#FF7141"
        :item-text="'name'"
        :item-value="'id'"
        @input="[setValueInStore(hoursValue, 'time')]"
        solo
        flat
        hide-details
      >
        <template #item="{ item }">
          <span class="select-item-text">{{ item['name'] }}</span>
        </template>
      </v-select>
    </div>
    <div class="range-date-half">
      <div>
        <p class="filter-label"></p>
        <v-menu
          v-model="toDateMenu"
          class="mt-2"
          transition="scale-transition"
          offset-y
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              solo
              flat
              hide-details
              readonly
              v-model="dateRangeTextTo"
              v-on="on"
              @click:append="showCalendar('to')"
            >
              <template v-slot:append>
                <v-icon
                  color="#FF7141"
                  class="cal-icon"
                  @click="showCalendar('to')"
                  >icon-calendar</v-icon
                >
              </template>
            </v-text-field>
          </template>
          <v-date-picker
            v-model="value[1]"
            color="#4D6C99"
            header-color="#4D6C99"
            elevation="3"
            show-adjacent-months
            :weekday-format="getDay"
            @input="[setValueInStore(value)]"
            first-day-of-week="1"
          />
        </v-menu>
      </div>
      <v-select
        v-model="hoursValue[1]"
        :items="fullTimeValues"
        color="#FF7141"
        :item-text="'name'"
        :item-value="'id'"
        @input="[setValueInStore(hoursValue, 'time')]"
        solo
        flat
        hide-details
      >
        <template #item="{ item }">
          <span class="select-item-text">{{ item['name'] }}</span>
        </template>
      </v-select>
    </div>
  </div>
  <div class="mr-5" v-else>
    <p class="filter-label">{{ labelText }}</p>
    <v-menu
      v-model="fromDateMenu"
      :close-on-content-click="false"
      class="mt-2"
      transition="scale-transition"
      offset-y
      max-width="290px"
      min-width="290px"
    >
      <template v-slot:activator="{ on }">
        <v-text-field
          solo
          flat
          hide-details
          readonly
          v-model="dateRangeText"
          v-on="on"
          @click:append="showCalendar"
        >
          <template v-slot:append>
            <v-icon color="#FF7141" class="cal-icon" @click="showCalendar"
              >icon-calendar</v-icon
            >
          </template>
        </v-text-field>
      </template>
      <v-date-picker
        v-model="value"
        color="#4D6C99"
        header-color="#4D6C99"
        elevation="3"
        show-adjacent-months
        :weekday-format="getDay"
        :title-date-format="titleDate"
        range
        @input="[setValueInStore(value)]"
        first-day-of-week="1"
      ></v-date-picker>
    </v-menu>
  </div>
</template>

<script>
export default {
  name: 'RangeDateFilter',
  props: ['labelText', 'filterName', 'splitted'],
  data: (vm) => ({
    isShow: false,
    value: [],
    dateFormatted: vm.formatDate(new Date().toISOString().substring(0, 10)),
    fromDateMenu: false,
    toDateMenu: false,
    hoursValue: [18, 6] // default values
  }),
  created() {
    this.value = this.$store.state.defaultFiltersValues[this.filterName];
  },
  computed: {
    fromDateDisp() {
      return this.formatDate(this.value);
    },
    dateRangeTextFrom() {
      return new Date(this.value[0]).toLocaleString('ru-RU', {
        day: 'numeric',
        month: 'numeric',
        year: 'numeric'
      });
    },
    dateRangeTextTo() {
      return new Date(this.value[1]).toLocaleString('ru-RU', {
        day: 'numeric',
        month: 'numeric',
        year: 'numeric'
      });
    },
    dateRangeText() {
      return this.dateRangeTextFrom + ' - ' + this.dateRangeTextTo;
    },
    storeFilterValues() {
      return this.$store.state.filtersValues[this.filterName];
    },
    fullTimeValues() {
      return this.$store.state.timeFilterValues[2].map((item) => {
        return { id: item.id, name: item.name + ':00' };
      });
    }
  },
  watch: {
    storeFilterValues: function (newVal) {
      this.value !== newVal ? (this.value = newVal) : false;
    },
    hoursValue: function (newVal) {
      this.dateRangeStr(newVal, 'hour');
    },
    value: function (newVal) {
      this.dateRangeStr(newVal, 'date');
    }
  },
  methods: {
    getDay(date) {
      const daysOfWeek = ['', '', '', '', '', '', ''];
      let i = new Date(date).getDay(date);
      return daysOfWeek[i];
    },
    setValueInStore(val, mode) {
      this.$store.commit('setFilterValues', {
        filter: mode === 'time' ? 'time' : this.filterName,
        val
      });
    },
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split('-');
      return `${day}.${month}.${year}`;
    },
    showCalendar(side) {
      if (!side || side === 'from') {
        this.fromDateMenu = !this.fromDateMenu;
      } else if (side === 'to') {
        this.toDateMenu = !this.toDateMenu;
      }
    },
    titleDate(dates) {
      return dates
        .map((n) =>
          new Date(n).toLocaleString('ru-RU', {
            weekday: 'short',
            month: 'short',
            day: 'numeric'
          })
        )
        .join(' - ');
    },
    dateRangeStr(value, mode) {
      let rangeDateStr =
        (mode === 'date'
          ? value[0]
          : this.$store.state.filtersValues['date'][0]) +
        ' ' +
        (mode === 'hour'
          ? value[0] + ':00:00'
          : this.hoursValue[0] + ':00:00') +
        '; ' +
        (mode === 'date'
          ? value[1]
          : this.$store.state.filtersValues['date'][1]) +
        ' ' +
        // eslint-disable-next-line
        (mode === 'hour'
          ? value[1] + ':00:00'
          : this.hoursValue[1] + ':00:00');

      this.$store.commit('setFilterValues', {
        filter: 'report-filter-interval',
        val: rangeDateStr
      });
    }
  }
};
</script>

